.about {
	margin-left: 10%;
	width: 90%;
	// min-height: 100vh;
	background-color: #fefefe;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 0;

	@media (max-width: 1023px) {
		width: 100%;
		margin-left: 0;
	}

	&-container {
		width: 90%;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding-top: 1rem;
		gap: 1.5rem;

		&-section {
			font-size: 1.5vw;

			@media (max-width: 1023px) {
				font-size: 2.5vw;
				text-align: center;
			}

			@media (max-width: 767px) {
				font-size: 1rem;
			}

			&-wrapper {
				padding-top: 1.5rem;
				display: flex;
				gap: 3rem;
				padding-bottom: 3vh;

				@media (max-width: 1439px) {
					padding-top: 1.5rem;
				}

				@media (max-width: 1023px) {
					gap: 1rem;
					padding-bottom: 1vh;
				}

				&-info {
					list-style: none;
					margin: 0;
					padding: 10px 0;
					width: auto;
					display: grid;
					grid-template-columns: repeat(2, 4fr);
					flex-wrap: wrap;
					grid-gap: 5px;
					white-space: nowrap;
					transition: 0.5s;
					row-gap: 1rem;

					@media (max-width: 767px) {
						display: flex;
						flex-direction: column;
						gap: 10px;
					}

					span {
						text-align: justify;

						@media print {
							font-size: 1rem;
							padding: 0;
						}

						@media (max-width: 1023px) {
							padding-top: 0;
						}
					}

					&-container {
						display: flex;
						flex-direction: column;
						align-items: center;
						gap: 2vh;
						text-align: center;

						@media print {
							gap: 0;
						}

						span {
							@media (max-width: 767px) {
							}
						}

						h2 {
							margin: 0px;
							text-align: center;
							font-family: Roboto;

							@media print {
								padding-bottom: 0;
								margin: 0;
							}

							@media (max-width: 767px) {
								padding-bottom: 0.5rem;
							}
						}

						&-item {
							flex-wrap: nowrap;
							font-size: 1.5vw;
							display: flex;
							align-items: center;
							gap: 5px;

							@media print {
								font-size: 3.5vw;
							}

							@media (max-width: 1023px) {
								font-size: 2.3vw;
							}

							@media (max-width: 767px) {
								font-size: 0.8rem;
							}

							span {
								a {
									position: relative;

									&:hover {
										color: #76aaea;
										transition: 0.5s;

										&::after {
											width: 100%;
										}
									}

									&::after {
										position: absolute;
										content: '';
										width: 0;
										height: 1px;
										background-color: #76aaea;
										bottom: 0;
										left: 0;
										transition: 0.5s;
									}
								}
							}
						}
					}
				}
			}

			&-image {
				width: 30vw;
				padding: 0;
				aspect-ratio: 0.9;
				object-fit: cover;
				border-radius: 30px;

				@media (max-width: 767px) {
					display: none;
				}
			}
		}

		&-title {
			color: rgb(13, 8, 8);
			font-size: 4rem;
			position: relative;
			display: inline;
			align-self: center;
			font-weight: 400;

			@media (max-width: 1023px) {
				font-size: 3rem;
			}

			@media (max-width: 767px) {
				font-size: 2rem;
			}

			&::after {
				position: absolute;
				content: '';
				background-color: black;
				width: 80%;
				left: 0px;
				bottom: -0.3rem;
				transition: width 0.5s ease-in-out;
				height: 0.1rem;
			}
		}
	}
}

.kekus {
	color: black;
	text-decoration: none;
	transition: 0.5s;
	position: relative;

	&::after {
		position: absolute;
		content: '';
		width: 0;
		height: 1px;
		background-color: #76aaea;
		bottom: 0;
		left: 0;
		transition: 0.5s;
	}

	&:hover {
		color: #76aaea;
		transition: 0.5s;

		&::after {
			width: 100%;
		}
	}
}

.mobile {
	display: none;

	@media print {
		display: none;
	}

	@media (max-width: 767px) {
		display: block;
		height: 110px;
	}
}
